
body {
  margin: 0px;
  padding: 0px;
  background-color: rgb(152, 209, 228)
}
html {
  margin: 0ch;
  padding: 0ch;
}
img {
  max-height: 50vmin
}


.header {
  font-family:'Courier New', Courier, monospace;
  background-color: lightskyblue;
  display: flex;
  min-width: 100%;
  max-height: 10%;
  outline: 4px solid lightseagreen;
  margin-bottom: 0ch;
  
}

.logo {
  max-height: 10vh;
}



#root {
  height: 100%;

}
.footer {
  
  bottom: 0px;
  position:absolute;
  display: flex;
  outline: 2px solid lightblue;
  background-color: rgb(152, 209, 228);
  width: 100%;
  margin: 0ch;
  flex-wrap: nowrap;
  overflow-x:hidden;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.tuskytime {
 
  display: flex;
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  padding: 2ch;
 
}



.baboon {
  height: 55ch;
  border: 0.5ch solid black;
}
#tuskytimelabel {
  font-size: 4vmin;
  margin: 10vmin;
}
#day {
  margin-bottom: 0.5vmin;
  margin-top: 0.1vmin;
  font-size: 10vmin;
  color: blue
}
#smallish-text {

  font-size: medium;
  flex: 1;
  vertical-align: bottom;
  text-align: center;
  margin-top: 0.2rem;
  

}
#tt {
  font-size: 15ch;
  margin-top: 4vh;
  margin-bottom: 4vh;
  color: blue
  
}

#small-text {
  justify-self: baseline;
  font-size: small;
  
  width: 20%;
  margin-left: 1%;
  color: rgb(25, 167, 255);
}

li {
  color:blue;
  list-style: none;
  font-size: x-large;
}